import React from 'react'
import './Main.css'

import logo from '../image/logo/logo.svg';
import avatar from '../image/icons/avatar-default-svgrepo-com.svg';
import fail from '../image/img/failure/fail.png'

function Failure() {

  return (
    <>
        <div className="m1">
            {/* header */}
            <div className="h1">
                <div className="h11">
                    {/* <div><img src={rightArrow} alt="arrow" /></div> */}
                    <p>Payment failed</p>
                </div>
                <div className="h12">
                    <div><img src={logo} alt="Logo" /></div>
                </div>
                <div className="h13">
                    <div><img src={avatar} alt="arrow" /></div>
                </div>
            </div>
            <div className="progressBar"><div style={{width: '0%'}}></div></div>

            {/* Body */}
            <div className="b1">
                <img src={fail} className="fs_img" />
                <div className="fs_title">Oh no!</div>
                <div className="fs_title">Something went wrong.</div>
                <div className="fs_para">We aren't able to process your payment. Please try again.</div>
                <div className="fs_btn"><button>Try again</button></div>
            </div>
        </div>
    </>
  );

}

export default Failure;