import React, {useState, useEffect, useRef} from 'react'
import { useSelector, useDispatch } from 'react-redux';

import {initializeBooking} from '../../../redux/BookingSlice'

// icons
import s1_arrow from "../../../image/icons/arrow-bottom-left-svgrepo-com.svg";
import continueButton from "../../../image/icons/circle-left-solid.svg";
import infoIcon from '../../../image/icons/info-svgrepo-com.svg';
import closeIcon from '../../../image/icons/add_white.svg';

// google apis
import { GoogleMap, useJsApiLoader, Autocomplete, DirectionsRenderer } from '@react-google-maps/api';

// components
import Header from '../../../components/Header'
import { useNavigate } from 'react-router-dom';

export default function Index() {

    const {isLoaded} = useJsApiLoader({
        googleMapsApiKey: 'AIzaSyB77DFujkr8aFxW3RCsDLbil5dXSbreyD4',
        libraries: ['places']
    })

    const dispatch = useDispatch()
    const navigate = useNavigate()

    const [map, setMap] = React.useState(null)

    const [s1Popup, setS1Popup] = useState(false);
    useEffect(() => {
        setS1Popup(true);
        setTimeout(() => {
            setS1Popup(false)
        }, 2000);
    }, []);

    const [directionResponse, setDirectionResponse] = useState(null);
    const [distance, setDistance] = useState('')
    const [duration, setDuration] = useState('')

    const originRef = useRef()
    const destinationRef = useRef()

    async function _handleOrigin() {
        if (originRef.current.value === '' || destinationRef.current.value === '')
            return;

        // eslint-disable-next-line no-undef
        const directionService = new google.maps.DirectionsService();
        const result = await directionService.route({
            origin: originRef.current.value,
            destination: destinationRef.current.value,
            // eslint-disable-next-line no-undef
            travelMode: google.maps.TravelMode.DRIVING
        });
        setDirectionResponse(result);
        setDistance(result.routes[0].legs[0].distance.text);
        setDuration(result.routes[0].legs[0].duration.text);
    }

    useEffect(() => {
        console.log("dr: ", directionResponse)
    }, [directionResponse])

    async function _handleDestination() {
        if (originRef.current.value === '' || destinationRef.current.value === '')
            return;

        // eslint-disable-next-line no-undef
        const directionService = new google.maps.DirectionsService();
        const result = await directionService.route({
            origin: originRef.current.value,
            destination: destinationRef.current.value,
            // eslint-disable-next-line no-undef
            travelMode: google.maps.TravelMode.DRIVING
        });
        setDirectionResponse(result);
        setDistance(result.routes[0].legs[0].distance.text)
        setDuration(result.routes[0].legs[0].duration.text)
    }

    const onUnmount = React.useCallback(function callback(map) {
        setMap(null)
    }, [])

    const onLoad = React.useCallback(function callback(map) {
        // This is just an example of getting and using the map instance!!! don't just blindly copy!
        const bounds = new window.google.maps.LatLngBounds();
        map.fitBounds(bounds);

        setMap(map)
    }, [])

    if (!isLoaded) return <div>Loading ...</div>;

    const _createBookingKey = () => {
        const today = new Date()
        const result = today.getFullYear()+"-"+today.getMonth()+today.getDate()+"-"+today.getHours()+today.getMinutes()+"-"+today.getMilliseconds();
        return result;
    }

    const _continueHandle = () => {

        if (originRef.current.value === '' || destinationRef.current.value === '') return;

        dispatch(initializeBooking({origin: originRef.current.value, destination: destinationRef.current.value}))
        navigate('/NextMove')
        // var myHeaders = new Headers();
        // myHeaders.append("Content-Type", "application/json");

        // var raw = JSON.stringify({
        //     "pickup": originRef.current.value,
        //     "destination": destinationRef.current.value,
        // });

        // var requestOptions = {
        //     method: 'POST',
        //     headers: myHeaders,
        //     body: raw,
        //     redirect: 'follow'
        // };

        // fetch("http://143.42.45.206/api/main/booking?bookingKey="+_createBookingKey(), requestOptions)
		// .then(response => response.text())
		// .then(result => )
		// .catch(error => console.log('error', error));

    }

    return (

        <div className="m1">
            {/* header */}
            <Header title="Pickup and Dropoff Address" isBackBtn={false} progress="10" />

            {/* Body */}
            <div className="b1">
                <div className='b11'>
                    <div className='b111'>Select Pickup and dropoff address</div>

                    <div className='s1_cont'>
                        <div className='s1_t1'>
                            <div>Pickup Address</div>
                            <Autocomplete onPlaceChanged={_handleOrigin}>
                                <input ref={originRef} type='text' placeholder='Search ...' />
                            </Autocomplete>
                            
                            <button>+ Add</button>
                        </div>
                        <div className='s1_t2'><img src={s1_arrow} alt='aa' /></div>
                        <div className='s1_t1'>
                            <div>Destination Address</div>

                            <Autocomplete onPlaceChanged={_handleDestination}>
                                <input ref={destinationRef} type='text' placeholder='Search ...' />
                            </Autocomplete>
                            <button>+ Add</button>
                        </div>
                        <div className='s1_btn'><button className='contButton' onClick={_continueHandle}><img src={continueButton} alt='button' /><span>Continue</span></button></div>
                    </div>

                    <div className='s1_map_cont'>
                        <GoogleMap
                            mapContainerStyle={{
                                width: '100%',
                                height: '100%',
                                borderRadius: '11px'
                            }}
                            defaultCenter={{ lat: 23.2599333, lng: 77.412615 }}
                            defaultZoom={10}
                            onLoad={onLoad}
                            onUnmount={onUnmount}
                        >
                            {directionResponse && <DirectionsRenderer directions={directionResponse} />}
                            {/* {markers.map(({ lat, lng }) => (
                                <Marker position={{ lat, lng }} />
                            ))} */}
                        </GoogleMap>
                    </div>

                </div>
            </div>

            {/* popup */}
            {s1Popup && (
                <div className='s1_pu_mcont'>
                    <div className='s1_pu_cont'>
                        <div className='s1_pu_im1'><img src={infoIcon} alt='info' /></div>
                        <div className='s1_pu_txt'>Too easy! We did 20+ Oyo moves in your area over the last 12 months</div>
                        <div className='s1_pu_im2' onClick={() => {setS1Popup(false);}}><img src={closeIcon} alt="close" /></div>
                    </div>
                </div>
            )}
        </div>

    );
}