import React, {useState, useEffect} from 'react'

//lib
import 'react-date-range/dist/styles.css';
import 'react-date-range/dist/theme/default.css';
import { Calendar } from 'react-date-range';
import { uploadFile } from 'react-s3';

// components
import Header from '../../../components/Header';
import MyMap from '../../../components/Map';

import { useNavigate } from 'react-router-dom';

// icon
import continueButton from "../../../image/icons/circle-left-solid.svg";
import checkGreen from '../../../image/icons/green_check.svg';
import editButton from '../../../image/icons/edit-svgrepo-com.svg';
import time1 from '../../../image/icons/sun-sunrise-svgrepo-com.svg';
import time2 from '../../../image/icons/light-bulb-svgrepo-com.svg';
import time3 from '../../../image/icons/beer-mug-svgrepo-com.svg';
import time4 from '../../../image/icons/earth-moon-svgrepo-com.svg';
import uploadIcon from '../../../image/icons/file-upload-svgrepo-com.svg';

const config = {
    bucketName: 'oyo-movers-static',
    region: 'eu-north-1',
    accessKeyId: 'AKIAR6GI4QI5R6NXCDE7',
    secretAccessKey: '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',
}

export default function AboutMove() {

    const navigate = useNavigate()

    const [inp51, setInp51] = useState(false);
    const [inp52, setInp52] = useState(false);
    const [inp53, setInp53] = useState(false);
    const [inp54, setInp54] = useState(false);

    const customRadioHandle5 = val => {
        if (val == 1) {
            setInp52(false);
            setInp53(false);
            setInp54(false);
        } else if (val == 2) {
            setInp51(false);
            setInp53(false);
            setInp54(false);
        } else if (val == 3) {
            setInp51(false);
            setInp52(false);
            setInp54(false);
        } else if (val == 4) {
            setInp51(false);
            setInp52(false);
            setInp53(false);
        }
    }

    const [uploadedFiles, setUploadedFiles] = useState([]);
    // aws file upload
    const _handleUploadFile = e => {
        e.preventDefault();
        window.Buffer = window.Buffer || require("buffer").Buffer;
        const file = e.target.files[0];

        uploadFile(file, config)
        .then(data => {
            console.log(data);
            setUploadedFiles(prevUploadedFiles => [...prevUploadedFiles, data.location])
        })
        .catch(err => console.error(err))
    }

    const _continueHandle = () => {
        navigate('/AccountCreation')
    }

    return (

        <>
            <div className="m1">
                {/* header */}
                <Header title="Date and Time" isBackBtn={true} previousRoute="/" progress="20" />

                {/* Body */}
                <div className="b1">
                    <div className='b11_d'>
                        <div className='b111'>Select Date and Time</div>

                        <div className='s5_cal_cont'>
                            <Calendar
                                date={new Date()}
                            />
                        </div>

                        {/* <input type="checkbox" id="s3_4" className='displayNone' checked={inp43} onChange={() => {setInp43(true);customRadioHandle3(4)}} name="radio" />
                            <label className='s3_but_item' for="s3_4">2 Bedrooms</label> */}


                        <div className='s4_but_cont'>
                            <input type="checkbox" id="s51" className='displayNone' checked={inp51} onChange={() => {setInp51(true);customRadioHandle5(1)}} name="radio" />
                            <label className='s4_but_item' for="s51">
                                <div className='s4_but_item_0'>
                                    <img src={time1} alt="time" />
                                </div>
                                <div className='s4_but_item_1'>
                                    <div className='s4_but_item_11'>Early Morning</div>
                                    <div className='s4_but_item_12'>6 AM TO 7 AM</div>
                                </div>
                            </label>
                            <input type="checkbox" id="s52" className='displayNone' checked={inp52} onChange={() => {setInp52(true);customRadioHandle5(2)}} name="radio" />
                            <label className='s4_but_item' for="s52">
                                <div className='s4_but_item_0'>
                                    <img src={time2} alt="time" />
                                </div>
                                <div className='s4_but_item_1'>
                                    <div className='s4_but_item_11'>Morning</div>
                                    <div className='s4_but_item_12'>7 AM TO 8 AM</div>
                                </div>
                            </label>
                        </div>
                        <div className='s4_but_cont'>
                            <input type="checkbox" id="s53" className='displayNone' checked={inp53} onChange={() => {setInp53(true);customRadioHandle5(3)}} name="radio" />
                            <label className='s4_but_item' for="s53">
                                <div className='s4_but_item_0'>
                                    <img src={time3} alt="time" />
                                </div>
                                <div className='s4_but_item_1'>
                                    <div className='s4_but_item_11'>Working hour</div>
                                    <div className='s4_but_item_12'>10 AM TO 1 PM</div>
                                </div>
                            </label>
                            <input type="checkbox" id="s54" className='displayNone' checked={inp54} onChange={() => {setInp54(true);customRadioHandle5(4)}} name="radio" />
                            <label className='s4_but_item' for="s54">
                                <div className='s4_but_item_0'>
                                    <img src={time4} alt="time" />
                                </div>
                                <div className='s4_but_item_1'>
                                    <div className='s4_but_item_11'>Afternoon</div>
                                    <div className='s4_but_item_12'>1 PM TO 4 AM</div>
                                </div>
                            </label>
                        </div>

                        {/* tell use */}
                        <div className='s4_tu_cont'>
                            <div className='s4_tu_he'>Tell us what you are moving</div>
                            <div className='s4_tu_sh'>List your items to be moved and tell us if you have stairs, lift or tricky access.</div>
                            <div className='s4_tu_ta'>
                                <textarea value="Few large items, one 2 seater couch" />
                                <div className='s4_tu_count'>Minimum 25 Characters</div>
                            </div>

                            <div className='s4_tu_cb_cont'>
                                <input type="checkbox" id="vehicle1" name="vehicle1" value="Bike"/>
                                <label for="vehicle1">Dismantling / Re-assembling Required</label>
                            </div>

                            <div className='s4_tu_cb_cont'>
                                <input type="checkbox" id="vehicle2" name="vehicle1" value="Bike"/>
                                <label for="vehicle2">Some packing help Required</label>
                            </div>

                            <div className='s4_tu_he mt30'>Add photos/PDFs</div> 
                            <div className='s4_tu_sh'>Providing Photos of your items helps your move prepare for your name</div>

                            <div>
                                <input type='file' className='displayNone' id="uploadFiles" onChange={e => _handleUploadFile(e)} />
                                <label for="uploadFiles" className="upload_img_cont">
                                    <img src={uploadIcon} />
                                    <p>Upload files</p>
                                </label>
                                <div className='s5_prev_img_cont'>
                                    {uploadedFiles.map((element, index) => {
                                        return (
                                            <img src={element} key={index} />
                                        );
                                    })}
                                </div>
                            </div>

                        </div>

                        <div className='b1_button_section'>
                            <button onClick={_continueHandle} className='contButton'><img src={continueButton} alt='button' /><span>Continue</span></button>
                        </div>
                    </div>
                    <div className='b12_d'>
                        <div className='s5_lf_map_cont'><MyMap /></div>

                        <div className='s5_lf_cont'>

                            <div className='s5_lf_item'>
                                <div className='s5_lf_item_mimg'><img src={checkGreen} alt="check" /></div>

                                <div className='s5_lf_item_scont'>
                                    <div className='s5_lf_sitem1'>Pickup and Dropoff Address</div>

                                    <div className='s5_lf_sitem2'>
                                        <div className='s5_lf_sitem_txt'>Address1</div>
                                        <div className='s5_lf_sitem_img'><img src={editButton} alt="check" /></div>
                                    </div>

                                    <div className='s5_lf_sitem2'>
                                        <div className='s5_lf_sitem_txt'>Address1</div>
                                        <div className='s5_lf_sitem_img'><img src={editButton} alt="check" /></div>
                                    </div>

                                </div>

                            </div>

                            <div className='s5_lf_item'>
                                <div className='s5_lf_item_mimg'><img src={checkGreen} alt="check" /></div>
                                <div className='s5_lf_item_scont'>
                                    <div className='s5_lf_sitem1'>Moving details</div>

                                    <div className='s5_lf_sitem2'>
                                        <div className='s5_lf_sitem_txt'>House Moving</div>
                                        <div className='s5_lf_sitem_img'><img src={editButton} alt="check" /></div>
                                    </div>

                                    <div className='s5_lf_sitem2'>
                                        <div className='s5_lf_sitem_txt'>2 bedrooms</div>
                                        <div className='s5_lf_sitem_img'><img src={editButton} alt="check" /></div>
                                    </div>

                                    <div className='s5_lf_sitem2'>
                                        <div className='s5_lf_sitem_txt'>Moderate House</div>
                                        <div className='s5_lf_sitem_img'><img src={editButton} alt="check" /></div>
                                    </div>

                                </div>
                            </div>

                            <div className='s5_lf_item'>
                                <div className='s5_lf_item_mimg'><img src={checkGreen} alt="check" /></div>
                                <div className='s5_lf_item_scont'>
                                    <div className='s5_lf_sitem1'>Vehicle</div>

                                    <div className='s5_lf_sitem2'>
                                        <div className='s5_lf_sitem_txt'>2 Men and Large</div>
                                        <div className='s5_lf_sitem_img'><img src={editButton} alt="check" /></div>
                                    </div>

                                </div>
                            </div>

                            

                        </div>


                    </div>
                </div>
            </div>
        </>
      
    );
}