import React, {useState, useEffect} from 'react'

// icons
import logo from '../../image/logo/logo.svg';
import avatar from '../../image/icons/avatar-default-svgrepo-com.svg';
import rightArrow from '../../image/icons/back-svgrepo-com.svg';
import './index.css'

// lib
import { useSelector, useDispatch } from 'react-redux';

import { useNavigate } from 'react-router-dom';
import { logout } from '../../redux/UserSlice';

function Index({title, isBackBtn, previousRoute, progress}) {

    const user = useSelector((state) => state.user.user)
    const dispatch = useDispatch()

    useEffect(() => {
        console.log("loginUser",user)
    }, [])

    const navigate = useNavigate();

    return (
        <>
            <div className="h1">
                <div className="h11">
                    {isBackBtn && <div onClick={() => navigate(previousRoute)}><img src={rightArrow} alt="arrow" /></div>}
                    <p>{title}</p>
                </div>
                <div className="h12">
                    <div><img src={logo} alt="Logo" /></div>
                </div>
                <div className="h13">
                    {/*  */}
                    {user === null ? <button className='head__btn' onClick={() => navigate("/login")}>Login</button> : <><div className='header__user'><img src={avatar} alt="arrow" /><p>{user.name}</p><button onClick={() => dispatch(logout())} className='head__btn'>logout</button></div></>}
                </div>
            </div>
            <div className="progressBar"><div style={{width: progress+'%'}}></div></div>
        </>
    );
}

export default Index;