import React, {useState, useEffect} from 'react'

//icons
import continueButton from "../../../image/icons/circle-left-solid.svg";
import checkGreen from '../../../image/icons/green_check.svg';
import editButton from '../../../image/icons/edit-svgrepo-com.svg';

// components
import Header from '../../../components/Header';
import MyMap from '../../../components/Map';

import {setMoveType} from '../../../redux/BookingSlice'

import { useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';

export default function MoveType() {
    
    const navigate = useNavigate()
    const dispatch = useDispatch()

    const [inp12_3, setInp12_3] = useState(false);
    const [inp22_3, setInp22_3] = useState(false);
    const [inp32_3, setInp32_3] = useState(false);
    const [inp42_3, setInp42_3] = useState(false);

    const customRadioHandle2_3 = val => {
        if (val == 1) {
            setInp22_3(false);
            setInp32_3(false);
            setInp42_3(false);
            dispatch(setMoveType({"movetype": "few-items"}))
        } else if (val == 2) {
            setInp32_3(false);
            setInp12_3(false);
            setInp42_3(false);
            dispatch(setMoveType({"movetype": "large-items"}))
        } else if (val == 3) {
            setInp12_3(false);
            setInp22_3(false);
            setInp42_3(false);
            dispatch(setMoveType({"movetype": "store-items"}))
        } else if (val == 4) {
            setInp12_3(false);
            setInp22_3(false);
            setInp32_3(false);
            dispatch(setMoveType({"movetype": "junk-items"}))
        }
        navigate('/Vehicle')
    }

    const _continueHandle = () => {
        navigate('/Vehicle')
    }

    return (
        
        <div className="m1">
            {/* header */}
            <Header title="Vehicle" isBackBtn={true} previousRoute="/" progress="20" />

            {/* Body */}
            <div className="b1">
                <div className='b11_d'>
                    <div className='b111'>What you want to move?</div>

                    <div className='s22_but_cont'>
                        <input type="checkbox" id="s2_13" className='displayNone' checked={inp12_3} onChange={() => {setInp12_3(true);customRadioHandle2_3(1)}} name="radio" />
                        <label className='s22_but_item' for="s2_13">Few-items</label>
                        <input type="checkbox" id="s2_23" className='displayNone' checked={inp22_3} onChange={() => {setInp22_3(true);customRadioHandle2_3(2)}} name="radio" />
                        <label className='s22_but_item' for="s2_23">Large items</label>
                    </div>
                    <div className='s22_but_cont'>
                        <input type="checkbox" id="s2_33" className='displayNone' checked={inp32_3} onChange={() => {setInp32_3(true);customRadioHandle2_3(3)}} name="radio" />
                        <label className='s22_but_item' for="s2_33">Store/Marketplace</label>
                        <input type="checkbox" id="s2_43" className='displayNone' checked={inp42_3} onChange={() => {setInp42_3(true);customRadioHandle2_3(4)}} name="radio" />
                        <label className='s22_but_item' for="s2_43">Junk removal</label>
                    </div>

                    <div className='b1_button_section'>
                        <button className='contButton' onClick={_continueHandle}><img src={continueButton} alt='button' /><span>Continue</span></button>
                    </div>
                </div>
                <div className='b12_d'>

                <div className='s5_lf_map_cont'><MyMap /></div>

                    <div className='s5_lf_cont'>

                        <div className='s5_lf_item'>
                            <div className='s5_lf_item_mimg'><img src={checkGreen} alt="check" /></div>

                            <div className='s5_lf_item_scont'>
                                <div className='s5_lf_sitem1'>Pickup and Dropoff Address</div>

                                <div className='s5_lf_sitem2'>
                                    <div className='s5_lf_sitem_txt'>Address1</div>
                                    <div className='s5_lf_sitem_img'><img src={editButton} alt="check" /></div>
                                </div>

                                <div className='s5_lf_sitem2'>
                                    <div className='s5_lf_sitem_txt'>Address1</div>
                                    <div className='s5_lf_sitem_img'><img src={editButton} alt="check" /></div>
                                </div>

                            </div>
                        </div>

                        <div className='s5_lf_item'>
                            <div className='s5_lf_item_mimg'><img src={checkGreen} alt="check" /></div>


                            <div className='s5_lf_item_scont'>
                                <div className='s5_lf_sitem1'>Moving details</div>

                                <div className='s5_lf_sitem2'>
                                    <div className='s5_lf_sitem_txt'>Office Relocation</div>
                                    <div className='s5_lf_sitem_img'><img src={editButton} alt="check" /></div>
                                </div>

                            </div>

                        </div>

                    </div>

                </div>
            </div>
        </div>
      
    );
}