import { createSlice } from '@reduxjs/toolkit'

const initialState = {
  booking: null,
  nextMoveType: null,
  nextMoveTypeNo: null,
  isPackingAndMoving: null,
  officeSize: null,
  moveType: null,

  noOfRoom: null,
  howFurnished: null,
  movingDate: null,
  movingTime: null,
  describeMoveType: null,
  isDismantlng: null,
  isHelpRequiredInPacking: null,
  pictures: null,

}

export const BookingSlice = createSlice({
  name: 'booking',
  initialState,
  reducers: {
      initializeBooking: (state, action) => {
        let locations = {
          pickup: action.payload.origin,
          dropoff: action.payload.destination
        }
        state.booking = locations
      },
      setNextType: (state, action) => {
        state.nextMoveType = action.payload.nextMoveType;
        state.nextMoveTypeNo = action.payload.nextMoveTypeNo;
        state.isPackingAndMoving = action.payload.isPackingAndMoving;
      },
      setOfficeSize: (state, action) => {
        state.officeSize = action.payload.officeSize;
      },
      setMoveType: (state, action) => {
        state.movetype = action.payload.movetype
      },
      setNoOfRoom: (state, action) => {
        state.noOfRoom = action.payload.noOfRoom
      },
      setFurnishLevel: (state, action) => {
        state.howFurnished = action.payload.furnishLevel
      }
  },
})

export const { initializeBooking, setNextType, setOfficeSize, setMoveType, setNoOfRoom, setFurnishLevel } = BookingSlice.actions

export default BookingSlice.reducer