export default function Example() {
  
    const [names, setNames] = useState([]);

    return (
      <div>
        <button onClick={() => setNames(prevNames => [...prevNames, 'Bob'])}>
          Add name
        </button>
  
        {names.map((element, index) => {
          return (
            <div key={index}>
              <h2>{element}</h2>
            </div>
          );
        })}
      </div>
    );
}

