import React, {useState, useEffect} from 'react'

//icons
import continueButton from "../../../image/icons/circle-left-solid.svg";


import { useNavigate } from 'react-router-dom';

// components
import Header from '../../../components/Header';
import MyMap from '../../../components/Map';
import Info from '../../../components/Info';

const paymentURI = "http://143.42.45.206";

export default function Payments() {

    const navigate = useNavigate()

    // payment
    const _createPaymentSession = e => {
        var myHeaders = new Headers();
        myHeaders.append("Content-Type", "application/json");

        var raw = JSON.stringify({
            "products": [
                {
                "name": "2 Men and Small Truck",
                "price": 75,
                "quantity": 1
                }
            ],
            "success_url": "/success",
            "cancel_url": "/failure"
        });

        var requestOptions = {
            method: 'POST',
            headers: myHeaders,
            body: raw,
            redirect: 'follow'
        };
    
        fetch(paymentURI+"/api/main/payment", requestOptions)
                .then(response => response.json())
                .then(result => {
                    console.log(result)
                    localStorage.setItem("paymentSessionId", result.data.sessionId);
                    window.open(result.data.checkoutUrl)
                })
                .catch(error => console.log('error', error));
    }
    
    const _continueHandle = () => {
        navigate('/Payment')
    }

    return (

        <div className="m1">
            {/* header */}
            <Header title="Payment" isBackBtn={true} previousRoute="/" progress="20" />

            {/* Body */}
            <div className="b1">
                <div className='b11_d'>

                    <div className='s4_tu_cont'>
                        <div className='s4_tu_he'>Payment Details</div>
                        <div className='s4_tu_sh'>Payment will not ne processed untill the completion of the job but we need your card details to secure your booking. You can also cancel your booking at any-time.</div>
                        
                        <div className='s7_ad_in_pn'>
                            <input type='text' placeholder='Enter promo code ... ...' />
                            <button>APPLY</button>
                            <div className='s7_pmcd'>ADHGD2</div>
                            {/* <div className='s6_ad_in_cont_err'>Please fill the required fields</div> */}
                        </div>

                        <div className='s4_tu_cb_cont'>
                            <input type="checkbox" id="vehicle1" name="vehicle1" value="Bike"/>
                            <label for="vehicle1">Pay online</label>
                            <button className='s4_tu_cb_but' onClick={_createPaymentSession}>Pay $250.00</button>
                        </div>

                        <div className='s4_tu_cb_cont'>
                            <input type="checkbox" id="vehicle2" name="vehicle1" value="Bike"/>
                            <label for="vehicle2">Paying Cash on Moving day</label>
                            <p>We still need your card details to secure your booking<br />Don't worry! Payments will not processed untill the completion of your job. You can still pay by Cah or PayID on moving day.</p>
                        </div>


                    </div>

                    <div className='b1_button_section'>
                        <button className='contButton' onClick={_continueHandle}><img src={continueButton} alt='button' /><span>Continue</span></button>
                    </div>
                </div>
                <div className='b12_d'>
                    <div className='s5_lf_map_cont'><MyMap /></div>

                    <Info />

                </div>
            </div>
        </div>
      
    );
}