import React, {useState, useEffect} from 'react'

//icons
import continueButton from "../../../image/icons/circle-left-solid.svg";
import checkGreen from '../../../image/icons/green_check.svg';
import editButton from '../../../image/icons/edit-svgrepo-com.svg';

import { useNavigate } from 'react-router-dom';

//components
import Header from '../../../components/Header';
import MyMap from '../../../components/Map';

import {setOfficeSize} from '../../../redux/BookingSlice'
import { useDispatch } from 'react-redux';

export default function OficeSize() {

    const [inp12_2, setInp12_2] = useState(false);
    const [inp22_2, setInp22_2] = useState(false);
    const [inp32_2, setInp32_2] = useState(false);
    const [inp42_2, setInp42_2] = useState(false);

    const dispatch = useDispatch()
    const navigate = useNavigate()

    const customRadioHandle2_2 = val => {
        if (val === 1) {
            setInp22_2(false);
            setInp32_2(false);
            setInp42_2(false);
            dispatch(setOfficeSize({"officeSize": "small"}))
        } else if (val === 2) {
            setInp32_2(false);
            setInp12_2(false);
            setInp42_2(false);
            dispatch(setOfficeSize({"officeSize": "medium"}))
        } else if (val === 3) {
            setInp12_2(false);
            setInp22_2(false);
            setInp42_2(false);
            dispatch(setOfficeSize({"officeSize": "large"}))
        } else if (val === 4) {
            setInp12_2(false);
            setInp22_2(false);
            setInp32_2(false);
            dispatch(setOfficeSize({"officeSize": "notSure"}))
        }
        navigate('/SelectRoom')
    }

    const _continueHandle = () => {
        navigate('/SelectRoom')
    }

    return (

        <div className="m1">
            {/* header */}
            <Header title="select office size" isBackBtn={true} previousRoute="/" progress="20" />

            {/* Body */}
            <div className="b1">
                <div className='b11_d'>
                    <div className='b111'>What is size of your office?</div>

                    <div className='s22_but_cont'>
                        <input type="checkbox" id="s2_1" className='displayNone' checked={inp12_2} onChange={() => {setInp12_2(true);customRadioHandle2_2(1)}} name="radio" />
                        <label className='s22_but_item' for="s2_1">Small Office</label>
                        <input type="checkbox" id="s2_2" className='displayNone' checked={inp22_2} onChange={() => {setInp22_2(true);customRadioHandle2_2(2)}} name="radio" />
                        <label className='s22_but_item' for="s2_2">Medium Office</label>
                    </div>
                    <div className='s22_but_cont'>
                        <input type="checkbox" id="s2_3" className='displayNone' checked={inp32_2} onChange={() => {setInp32_2(true);customRadioHandle2_2(3)}} name="radio" />
                        <label className='s22_but_item' for="s2_3">Large Office</label>
                        <input type="checkbox" id="s2_4" className='displayNone' checked={inp42_2} onChange={() => {setInp42_2(true);customRadioHandle2_2(4)}} name="radio" />
                        <label className='s22_but_item' for="s2_4">Not sure for size</label>
                    </div>

                    <div className='b1_button_section'>
                        <button className='contButton' onClick={_continueHandle}><img src={continueButton} alt='button' /><span>Continue</span></button>
                    </div>
                </div>
                <div className='b12_d'>

                <div className='s5_lf_map_cont'><MyMap /></div>

                    <div className='s5_lf_cont'>

                        <div className='s5_lf_item'>
                            <div className='s5_lf_item_mimg'><img src={checkGreen} alt="check" /></div>

                            <div className='s5_lf_item_scont'>
                                <div className='s5_lf_sitem1'>Pickup and Dropoff Address</div>

                                <div className='s5_lf_sitem2'>
                                    <div className='s5_lf_sitem_txt'>Address1</div>
                                    <div className='s5_lf_sitem_img'><img src={editButton} alt="check" /></div>
                                </div>

                                <div className='s5_lf_sitem2'>
                                    <div className='s5_lf_sitem_txt'>Address1</div>
                                    <div className='s5_lf_sitem_img'><img src={editButton} alt="check" /></div>
                                </div>

                            </div>
                        </div>

                        <div className='s5_lf_item'>
                            <div className='s5_lf_item_mimg'><img src={checkGreen} alt="check" /></div>


                            <div className='s5_lf_item_scont'>
                                <div className='s5_lf_sitem1'>Moving details</div>

                                <div className='s5_lf_sitem2'>
                                    <div className='s5_lf_sitem_txt'>Office Relocation</div>
                                    <div className='s5_lf_sitem_img'><img src={editButton} alt="check" /></div>
                                </div>

                            </div>

                        </div>

                    </div>

                </div>
            </div>
        </div>
      
    );
}