import React, {useState, useEffect} from 'react'
//icons
import continueButton from "../../../image/icons/circle-left-solid.svg";
import m1 from '../../../image/img/moving_img/m1.png';
import m2 from '../../../image/img/moving_img/m2.png';

// components
import Header from '../../../components/Header';

import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import {setNextType} from '../../../redux/BookingSlice'

export default function PackAndMove() {

    const dispatch = useDispatch()
    const navigate = useNavigate()

    const [inp12_1, setInp12_1] = useState(false);
    const [inp22_1, setInp22_1] = useState(false);

    const customRadioHandle2_1 = val => {
        if (val === 1) {
            setInp22_1(false);
            dispatch(setNextType({nextMoveType: "HouseMoving", nextMoveTypeNo: 1, isPackingAndMoving: true}))
            navigate('/SelectRoom')
        } else if (val === 2) {
            setInp12_1(false);
            dispatch(setNextType({nextMoveType: "OfficeRelocation", nextMoveTypeNo: 2, isPackingAndMoving: true}))
            navigate('/OfficeSize')
        }
    }

    const _continueHandle = () => {
        navigate('/SelectRoom')
    }

    return (

        <div className="m1">
            {/* header */}
            <Header title="Packing and Moving" isBackBtn={true} previousRoute="/" progress="20" />

            {/* Body */}
            <div className="b1">
                <div className='b11'>
                    <div className='b111'>What you want to pack and move?</div>

                    <div className='s2_sub_head'>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy.</div>

                    <div className='s2_sel_cont'>
                        <input type="checkbox" id="i1" className='displayNone' checked={inp12_1} onChange={() => {setInp12_1(true);customRadioHandle2_1(1)}} name="radio" />
                        <label for="i1" className='s2_sel_item'>
                            <img src={m1} alt="image" />
                            <p>House and Apartment moving</p>
                            <h6>Lorem Ipsum is simply dummy text of the printing and typesetting industry.</h6>
                        </label>
                        <input type="checkbox" id="i2" className='displayNone' checked={inp22_1} onChange={() => {setInp22_1(true);customRadioHandle2_1(2)}} name="radio" />
                        <label for="i2" className='s2_sel_item'>
                            <img src={m2} alt="image" />
                            <p>Office relocation</p>
                            <h6>Lorem Ipsum is simply dummy text of the printing and typesetting industry.</h6>
                        </label>
                    </div>

                    <div className='b1_button_section'>
                        <button className='contButton' onClick={_continueHandle}><img src={continueButton} alt='button' /><span>Continue</span></button>
                    </div>
                </div>
            </div>
        </div>
      
    );
}