import React, {useState, useEffect} from 'react'

//icons
import f1 from '../../../image/img/furnish/f1.svg';
import f2 from '../../../image/img/furnish/f2.svg';
import f3 from '../../../image/img/furnish/f3.svg';
import f4 from '../../../image/img/furnish/f4.svg';
import f5 from '../../../image/img/furnish/f5.svg';
import f6 from '../../../image/img/furnish/f6.svg';
import f7 from '../../../image/img/furnish/f7.svg';
import continueButton from '../../../image/icons/circle-left-solid.svg';
import checkGreen from '../../../image/icons/green_check.svg';
import editButton from '../../../image/icons/edit-svgrepo-com.svg';

// components
import Header from '../../../components/Header'
import MyMap from '../../../components/Map';

import { useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import {setNoOfRoom, setFurnishLevel} from '../../../redux/BookingSlice'

export default function SelectRoom() {

    const navigate = useNavigate();
    const dispatch = useDispatch()

    const [howFurnished, setHowFurnished] = useState(false); 

    const [inp13, setInp13] = useState(false);
    const [inp23, setInp23] = useState(false);
    const [inp33, setInp33] = useState(false);
    const [inp43, setInp43] = useState(false);
    const [roomCount, setRoomCount] = useState(0);
    const [furnishLevelCount, setFurnishLevelCount] = useState(1);

    const customRadioHandle3 = val => {
        if (val == 1) {
            setInp23(false);
            setInp33(false);
            setInp43(false);
        } else if (val == 2) {
            setInp33(false);
            setInp13(false);
            setInp43(false);
        } else if (val == 3) {
            setInp13(false);
            setInp23(false);
            setInp43(false);
        } else if (val == 4) {
            setInp13(false);
            setInp23(false);
            setInp33(false);
        }
        setRoomCount(val)
        dispatch(setNoOfRoom({"noOfRoom": val}))
        setHowFurnished(true)
    }

    const _continueHandle = () => {
        dispatch(setFurnishLevel({"furnishLevel": furnishLevelCount}));
        navigate('/Vehicle');
    }

    useEffect(() => {
        console.log("furnishLevel:", furnishLevelCount)
    }, [furnishLevelCount])


    return (
        <div className="m1">
            {/* header */}
            <Header title="Select rooms" isBackBtn={true} previousRoute="/" progress="20" />

            {/* Body */}
            <div className="b1">
                <div className='b11_d'>
                    <div className='b111'>How many rooms you want to shift?</div>

                    <div className='s3_but_cont'>
                        <input type="checkbox" id="s3_1" className='displayNone' checked={inp13} onChange={() => {setInp13(true);customRadioHandle3(1)}} name="radio" />
                        <label className='s3_but_item' for="s3_1">1 Bedrooms</label>
                        <input type="checkbox" id="s3_2" className='displayNone' checked={inp23} onChange={() => {setInp23(true);customRadioHandle3(2)}} name="radio" />
                        <label className='s3_but_item' for="s3_2">2 Bedrooms</label>
                    </div>
                    <div className='s3_but_cont'>
                        <input type="checkbox" id="s3_3" className='displayNone' checked={inp33} onChange={() => {setInp33(true);customRadioHandle3(3)}} name="radio" />
                        <label className='s3_but_item' for="s3_3">3 Bedrooms</label>
                        <input type="checkbox" id="s3_4" className='displayNone' checked={inp43} onChange={() => {setInp43(true);customRadioHandle3(4)}} name="radio" />
                        <label className='s3_but_item' for="s3_4">+4 Bedrooms</label>
                    </div>

                    {howFurnished && 
                    
                        <>
                            <div className='b111'>How furnished you {roomCount} bedrooms are?</div>

                            <div className='s3_fur_img_cont'>
                                <div className='s3_fur_img_item'>
                                    <img src={f1} />
                                    <img src={f2} />
                                    <img src={f3} />
                                    <img src={f4} />
                                </div>
                                <div className='s3_fur_img_item'>
                                    <img src={f5} />
                                    <img src={f6} />
                                    <img src={f7} />
                                </div>
                            </div>

                            <div className='s3_fur_pro_cont'>
                                <input type="range" min="1" value={furnishLevelCount} onChange={e => setFurnishLevelCount(e.target.value)} max="3" className="slider"/>
                                <div className='s3_fur_pro_name'>
                                    <p>Lightly</p>
                                    <p>Moderately</p>
                                    <p>Heavily</p>
                                </div>
                            </div>
                        </>

                    }


                    <div className='b1_button_section'>
                        <button className='contButton' onClick={_continueHandle}><img src={continueButton} alt='button' /><span>Continue</span></button>
                    </div>
                </div>
                <div className='b12_d'>

                <div className='s5_lf_map_cont'><MyMap /></div>

                    <div className='s5_lf_cont'>

                        <div className='s5_lf_item'>
                            <div className='s5_lf_item_mimg'><img src={checkGreen} alt="check" /></div>

                            <div className='s5_lf_item_scont'>
                                <div className='s5_lf_sitem1'>Pickup and Dropoff Address</div>

                                <div className='s5_lf_sitem2'>
                                    <div className='s5_lf_sitem_txt'>Address1</div>
                                    <div className='s5_lf_sitem_img'><img src={editButton} alt="check" /></div>
                                </div>

                                <div className='s5_lf_sitem2'>
                                    <div className='s5_lf_sitem_txt'>Address1</div>
                                    <div className='s5_lf_sitem_img'><img src={editButton} alt="check" /></div>
                                </div>

                            </div>

                        </div>

                        <div className='s5_lf_item'>
                            <div className='s5_lf_item_mimg'><img src={checkGreen} alt="check" /></div>


                            <div className='s5_lf_item_scont'>
                                <div className='s5_lf_sitem1'>Moving details</div>

                                <div className='s5_lf_sitem2'>
                                    <div className='s5_lf_sitem_txt'>House Moving</div>
                                    <div className='s5_lf_sitem_img'><img src={editButton} alt="check" /></div>
                                </div>

                            </div>

                        </div>

                    </div>

                </div>
            </div>
        </div>
    );
}