import React from 'react'
import './App.css'
import { BrowserRouter, Routes, Route } from "react-router-dom";

// basic routes
import Example from "./screens/Example";
import Home from './screens/Home';
import Failure from './screens/Failure';
import Success from './screens/Success';
import Login from './screens/Login';

// booking routes
import CreateBooking from './screens/Booking/CreateBooking';
import NextMove from './screens/Booking/NextMove';
import SelectRoom from './screens/Booking/SelectRooms';
import Vehicle from './screens/Booking/Vehicles';
import AboutMove from './screens/Booking/AboutMove';
import AccountCreation from './screens/Booking/AccountCreation';
import Payments from './screens/Booking/Payments';
import OficeSize from './screens/Booking/OfficeSize';
import PackAndMove from './screens/Booking/PackAndMove';
import MoveType from './screens/Booking/MoveType';

function App() {

  return (
    <BrowserRouter>
      <Routes>

        <Route path="/" element={<CreateBooking />}></Route>
        <Route path="/example" element={<Example />}></Route>
        <Route path="/become_a_mover" element={<Example />}></Route>
        <Route path="/fast_booking" element={<Example />}></Route>
        <Route path="/failure" element={<Failure />}></Route>
        <Route path="/success" element={<Success />}></Route>
        <Route path="/Login" element={<Login />}></Route>

        <Route path="/CreateBooking" element={<CreateBooking />}></Route>
        <Route path="/NextMove" element={<NextMove />}></Route>
        <Route path="/SelectRoom" element={<SelectRoom />}></Route>
        <Route path="/Vehicle" element={<Vehicle />}></Route>
        <Route path="/AboutMove" element={<AboutMove />}></Route>
        <Route path="/AccountCreation" element={<AccountCreation />}></Route>
        <Route path="/Payment" element={<Payments />}></Route>
        <Route path="/OfficeSize" element={<OficeSize />}></Route>
        <Route path="/PackAndMove" element={<PackAndMove />}></Route>
        <Route path="/MoveType" element={<MoveType />}></Route>

      </Routes>
    </BrowserRouter>
  );
}

export default App;
