import React, {useState, useEffect} from 'react'

//icons
import continueButton from "../../../image/icons/circle-left-solid.svg";
import checkGreen from '../../../image/icons/green_check.svg';
import editButton from '../../../image/icons/edit-svgrepo-com.svg';
import t1 from '../../../image/img/truck/t1.png';
import t2 from '../../../image/img/truck/t2.png';
import t3 from '../../../image/img/truck/t3.png';

// components
import Header from '../../../components/Header';
import MyMap from '../../../components/Map';

import { useNavigate } from 'react-router-dom';

export default function Vehicle() {

    const navigate = useNavigate()
  
    const [inps41, setInps41] = useState(false);
    const [inps42, setInps42] = useState(false);
    const [inps43, setInps43] = useState(false);

    const customRadioHandle4 = val => {
        if (val == 1) {
            setInps42(false);
            setInps43(false);
        } else if (val == 2) {
            setInps41(false);
            setInps43(false);
        } else if (val == 3) {
            setInps41(false);
            setInps42(false);
        }
    }

    const _continueHandle = () => {
        navigate('/AccountCreation')
    }


    return (
        <div className="m1">
            {/* header */}
            <Header title="Vehicle" isBackBtn={true} previousRoute="/" progress="20" />

            {/* Body */}
            <div className="b1">
                <div className='b11_d'>
                    <div className='b111'>Select Vehicle</div>

                    <div className='s4_cont'>
                        <input type="checkbox" id="s4_1" className='displayNone' checked={inps41} onChange={() => {setInps41(true);customRadioHandle4(1)}} name="radio" />
                        <label className='s4_item' for="s4_1">
                            <img src={t1} alt='truck' />
                            <p>2 Men and Small Truck</p>
                            <h6><b>$85</b> per half hour</h6>
                        </label>
                        <input type="checkbox" id="s4_2" className='displayNone' checked={inps42} onChange={() => {setInps42(true);customRadioHandle4(2)}} name="radio" />
                        <label className='s4_item' for="s4_2">
                            <img src={t2} alt='truck' />
                            <p>2 Men and Small Truck</p>
                            <h6><b>$85</b> per half hour</h6>
                        </label>
                        <input type="checkbox" id="s4_3" className='displayNone' checked={inps43} onChange={() => {setInps43(true);customRadioHandle4(3)}} name="radio" />
                        <label className='s4_item' for="s4_3">
                            <img src={t3} alt='truck' />
                            <p>2 Men and Small Truck</p>
                            <h6><b>$85</b> per half hour</h6>
                        </label>
                    </div>

                    <div className='s4_info_cont'>
                        <div className='s4_coll'><img src={t3} alt='truck' /></div>
                        <div className='s4_colr'>
                            <div className='s4_colr_title'>To Speedup Moving Process</div>
                            <div className='s4_colr_body'>
                                <input type="checkbox" id="Exvehicle" name="Exvehicle" value="Bike"/>
                                <label for="Exvehicle">+ Add Extra Man</label>
                                <p><u><b>$ 25</b>per Half Hour</u></p>
                            </div>
                        </div>
                    </div>

                    <div className='b1_button_section'>
                        <button onClick={_continueHandle} className='contButton'><img src={continueButton} alt='button' /><span>Continue</span></button>
                    </div>
                </div>
                <div className='b12_d'>

                <div className='s5_lf_map_cont'><MyMap /></div>

                    <div className='s5_lf_cont'>

                        <div className='s5_lf_item'>
                            <div className='s5_lf_item_mimg'><img src={checkGreen} alt="check" /></div>

                            <div className='s5_lf_item_scont'>
                                <div className='s5_lf_sitem1'>Pickup and Dropoff Address</div>

                                <div className='s5_lf_sitem2'>
                                    <div className='s5_lf_sitem_txt'>Address1</div>
                                    <div className='s5_lf_sitem_img'><img src={editButton} alt="check" /></div>
                                </div>

                                <div className='s5_lf_sitem2'>
                                    <div className='s5_lf_sitem_txt'>Address1</div>
                                    <div className='s5_lf_sitem_img'><img src={editButton} alt="check" /></div>
                                </div>

                            </div>
                        </div>

                        <div className='s5_lf_item'>
                            <div className='s5_lf_item_mimg'><img src={checkGreen} alt="check" /></div>
                            <div className='s5_lf_item_scont'>
                                <div className='s5_lf_sitem1'>Moving details</div>

                                <div className='s5_lf_sitem2'>
                                    <div className='s5_lf_sitem_txt'>House Moving</div>
                                    <div className='s5_lf_sitem_img'><img src={editButton} alt="check" /></div>
                                </div>

                                <div className='s5_lf_sitem2'>
                                    <div className='s5_lf_sitem_txt'>2 bedrooms</div>
                                    <div className='s5_lf_sitem_img'><img src={editButton} alt="check" /></div>
                                </div>

                                <div className='s5_lf_sitem2'>
                                    <div className='s5_lf_sitem_txt'>Moderate House</div>
                                    <div className='s5_lf_sitem_img'><img src={editButton} alt="check" /></div>
                                </div>

                            </div>
                        </div>

                        

                    </div>

                </div>
            </div>
        </div>
    );
}