import React, {useState, useEffect} from 'react'

// icons
import continueButton from "../../../image/icons/circle-left-solid.svg";
import m1 from '../../../image/img/moving_img/m1.png';
import m2 from '../../../image/img/moving_img/m2.png';
import m3 from '../../../image/img/moving_img/m3.png';

// components
import Header from '../../../components/Header'

import { useSelector, useDispatch } from 'react-redux';
import {setNextType} from '../../../redux/BookingSlice'
import { useNavigate } from 'react-router-dom';

function NextMove() {

    const dispatch = useDispatch()
    const navigate = useNavigate()
  
    const [inp1, setInp1] = useState(false);
    const [inp2, setInp2] = useState(false);
    const [inp3, setInp3] = useState(false);
    const [inp4, setInp4] = useState(false);


    const customRadioHandle = val => {
        if(val === 1) {
            setInp2(false);
            setInp3(false);
            setInp4(false);
            dispatch(setNextType({nextMoveType: "HouseMoving", nextMoveTypeNo: 1, isPackingAndMoving: false}))
            navigate('/SelectRoom')
        } else if(val === 2) {
            setInp1(false);
            setInp3(false);
            setInp4(false);
            dispatch(setNextType({nextMoveType: "OfficeRelocation", nextMoveTypeNo: 1, isPackingAndMoving: false}))
            navigate('/OfficeSize')
        } else if(val === 3) {
            setInp2(false);
            setInp1(false);
            setInp4(false);
            dispatch(setNextType({nextMoveType: "PackingAndMoving", nextMoveTypeNo: 1, isPackingAndMoving: true}))
            navigate('/PackAndMove')
        } else if(val === 4) {
            setInp2(false);
            setInp1(false);
            setInp3(false);
            dispatch(setNextType({nextMoveType: "SomethingElse", nextMoveTypeNo: 1, isPackingAndMoving: false}))
            navigate('/MoveType')
        }

    }

    return (
        <div className="m1">
        {/* header */}
        <Header title="What's your next move?" isBackBtn={true} previousRoute="/" progress="20" />

        {/* Body */}
        <div className="b1">
            <div className='b11'>
                <div className='b111'>What's your next move?</div>

                <div className='s2_sub_head'>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy.</div>

                <div className='s2_sel_cont'>
                    <input type="checkbox" id="i1" className='displayNone' checked={inp1} onChange={() => {setInp1(true);customRadioHandle(1)}} name="radio" />
                    <label htmlFor="i1" className='s2_sel_item'>
                        <img src={m1} alt="image" />
                        <p>House and Apartment moving</p>
                        <h6>Lorem Ipsum is simply dummy text of the printing and typesetting industry.</h6>
                    </label>
                    <input type="checkbox" id="i2" className='displayNone' checked={inp2} onChange={() => {setInp2(true);customRadioHandle(2)}} name="radio" />
                    <label htmlFor="i2" className='s2_sel_item'>
                        <img src={m2} alt="image" />
                        <p>Office relocation</p>
                        <h6>Lorem Ipsum is simply dummy text of the printing and typesetting industry.</h6>
                    </label>
                    <input type="checkbox" id="i3" className='displayNone' checked={inp3} onChange={() => {setInp3(true);customRadioHandle(3)}} name="radio" />
                    <label htmlFor="i3" className='s2_sel_item'>
                        <img src={m3} alt="image" />
                        <p>Packing and Moving</p>
                        <h6>Lorem Ipsum is simply dummy text of the printing and typesetting industry.</h6>
                    </label>
                    <input type="checkbox" id="i4" className='displayNone' checked={inp4} onChange={() => {setInp4(true);customRadioHandle(4)}} name="radio" />
                    <label htmlFor="i4" className='s2_sel_item'>
                        <img src={m3} alt="image" />
                        <p>Something else</p>
                        <h6>Lorem Ipsum is simply dummy text of the printing and typesetting industry.</h6>
                    </label>
                </div>

                <div className='b1_button_section'>
                    {/* <button className='contButton' onClick={() => {setTab2(false);setTab3(true);}}><img src={continueButton} alt='button' /><span>Continue</span></button> */}
                    <button className='contButton' onClick={() => {}}><img src={continueButton} alt='button' /><span>Continue</span></button>
                </div>
            </div>
        </div>
    </div>
    );
}

export default NextMove;